import "./Card.css";
// React hooks
import { useState, useEffect } from "react";
import useComponentDimension from "../hooks/useComponentDimension";


import { motion, LayoutGroup, AnimatePresence } from "framer-motion";
import styled from "styled-components";

// Redux 
import { useSelector } from "react-redux";
import {
  selectMyCards        
} from "../../redux/slices/GameStateSlice"

import { GameContext } from "../../screen/game/Game";
import { CardInHandFactory } from "./CardFactory";

let cardProperties = {
  width: 100,

};

const StyledMyCardContainer = styled(motion.div) `
  & .in-hand-card{
    &:not(:first-child) {
      margin-left: ${()=>-cardProperties.width}px;
    }
    &:hover ~ .in-hand-card{
      left: ${()=>cardProperties.width}px;
    }
  }
`
const MyCardContainer = () => {
  const [myCards, setMyCards] = useState<JSX.Element[]>([]);

  // Redux
  const myCardsSelector = useSelector(selectMyCards);
  
  useEffect(() => {
    const newMyCards = myCardsSelector.map(card => {
      return (
        <CardInHandFactory {...card} key={card.id}/>
      );
    })
    setMyCards(newMyCards);
  }, [myCardsSelector,])

  const {width} = useComponentDimension('my-card-container');
  // Calculates the nominal card offset to ensure it does
  // not overflow the container
  const cardWidth = window.innerHeight / 5 + 16; // 5 from 20vh 16 for border
  cardProperties.width = Math.max(window.innerHeight / 10, (cardWidth * (myCards.length) - (width - 60)) / (myCards.length - 1)); // 60 from padding: ;

  return (
    <StyledMyCardContainer id="my-card-container">
      <LayoutGroup>
        <AnimatePresence>
          {myCards}
        </AnimatePresence>
      </LayoutGroup>
    </StyledMyCardContainer>
  );
};

export  {MyCardContainer};

function uuid(): any {
  return Math.random();
}
