import "./Card.css";
import { useState, useEffect, useRef, useContext } from "react";


import { useDispatch } from "react-redux";
import { selectCard, deselectCard } from "../../redux/slices/GameStateSlice"

import HelpIcon from "./../../assets/question-mark.svg";
import { motion, AnimatePresence } from "framer-motion";

function randn_bm(): number {
  let u = 0,
    v = 0;
  while (u === 0) u = Math.random(); //Converting [0,1) to (0,1)
  while (v === 0) v = Math.random();
  let num = Math.sqrt(-2.0 * Math.log(u)) * Math.cos(2.0 * Math.PI * v);
  num = num / 10.0 + 0.5; // Translate to 0 -> 1
  if (num > 1 || num < 0) return randn_bm(); // resample between 0 and 1
  return num;
}

interface ICard {
  cardId: string;
  cardName: string;
  cardBorderColour: string;
  cardImage: any;
  cardIcon: any;
  cardFlavourText: string | null;
  cardEffectText: string | null;
}

interface IPlayedCard {
  cardName: string;
  cardBorderColour: string;
  cardImage: any;
  cardIcon: any;
  cardFlavourText: string | null;
  cardEffectText: string | null;
}

interface IInHandCard {
  cardId: string;
  cardName: string;
  cardBorderColour: string;
  cardImage: any;
  cardIcon: any;
}

const Card = (props: ICard) => {
  return (
    <motion.div
      className="card"
      style={{ outline: `3px solid ${props.cardBorderColour}` }}
    >
      <motion.img className="card-help-icon" src={HelpIcon} alt="HelpIcon" />
      <motion.div className="card-top card-banner">
        <motion.img className="card-icon" src={props.cardIcon} alt={props.cardName} />
        <motion.h1 className="card-name">{props.cardName}</motion.h1>
      </motion.div>
      <motion.div className="card-middle">
        <motion.img
          src={props.cardImage}
          alt=""
          className="card-image"
          draggable="false"
        />
      </motion.div>
      <motion.div className="card-bottom card-banner">
        <motion.img className="card-icon" src={props.cardIcon} alt={props.cardName} />
        <motion.h1 className="card-name">{props.cardName}</motion.h1>
      </motion.div>
    </motion.div>
  );
};

const InHandCardVariant = {
  enter: {
    opacity: 0,
    y: "-50%",
  },

  unselected: {
    opacity: 1,
    y: "0%",
    transition: {
      type: "spring",
      duration: 0.55,
    },
  },

  selected: {
    opacity: 1,
    y: "-10%",
  },

  exit: {
    opacity: 1,
    y: "-50%",
    transition: {
      type: "spring",
      duration: 1.55,
    },
  },
};

const InHandCard = (props: IInHandCard): JSX.Element => {
  const [variant, setVariant] = useState("unselected");
  const [isSelected, setIsSelected] = useState(false)

  const dispatch = useDispatch();

  const handleClick = () => {
    if (variant === "unselected") {
      dispatch(selectCard(props.cardId));
      setVariant("selected");
    } else {
      dispatch(deselectCard(props.cardId))
      setVariant("unselected");
    }
  };

  return (
      <motion.div
        className="in-hand-card"
        initial="enter"
        animate={variant}
        exit="exit"
        variants={InHandCardVariant}
        whileHover={{ scale: 1.07 }}
        onClick={handleClick}
      >
        <Card
          cardId={props.cardId}
          cardName={props.cardName}
          cardBorderColour={props.cardBorderColour}
          cardImage={props.cardImage}
          cardIcon={props.cardIcon}
          cardFlavourText={null}
          cardEffectText={null}
        />
      </motion.div>
  );
};

const PlayedCard = (props: IPlayedCard) => {
  const id = Math.random() * 1000;
  return (
    <div
      className="card-played"
      style={{
        transform: `rotateZ(${(randn_bm() - 0.5) * 60}deg)`,
      }}
    >
      <Card
        cardId={"card-" + id}
        cardName={props.cardName}
        cardBorderColour={props.cardBorderColour}
        cardImage={props.cardImage}
        cardIcon={props.cardIcon}
        cardFlavourText={null}
        cardEffectText={null}
      />
    </div>
  );
};

export { Card, InHandCard, PlayedCard };
